* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Root Variables for Responsive Design */
:root {
  --header-height-desktop: 100px; /* Updated to match actual header height */
  --header-height-tablet: 60px;
  --header-height-mobile: 56px;
  --nav-width-desktop: 250px;
  --nav-width-tablet: 0px; /* Hidden on tablet */
  --content-padding-desktop: 1.5rem;
  --content-padding-tablet: 1rem;
  --content-padding-mobile: 0.75rem;
  --content-padding-small: 0.5rem;
  --touch-target-min: 44px; /* Minimum touch target size */
}

.App {
  height: 100vh;
  height: 100dvh; /* Dynamic viewport height for mobile browsers */
  background-color: rgb(var(--color-background));
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  /* Prevent horizontal scroll */
  max-width: 100vw;
  overflow-x: hidden;
}

.app-container {
  display: flex;
  flex-direction: row; /* Desktop: side-by-side */
  height: calc(100vh - var(--header-height-desktop));
  height: calc(100dvh - var(--header-height-desktop));
  margin-left: var(--nav-width-desktop);
  transition: margin-left 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: margin-left;
  flex: 1;
  overflow: hidden;
  position: relative;
}

.main-content {
  flex: 1;
  padding: 0.75rem;
  overflow-y: auto;
  overflow-x: hidden;
  transition: padding 0.3s ease;
  height: 100%;
  width: 100%;
  /* Smooth scrolling */
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
  /* Better scrollbar on desktop */
  scrollbar-width: thin;
  scrollbar-color: rgba(74, 144, 226, 0.3) transparent;
}

.main-content::-webkit-scrollbar {
  width: 8px;
}

.main-content::-webkit-scrollbar-track {
  background: transparent;
}

.main-content::-webkit-scrollbar-thumb {
  background: rgba(74, 144, 226, 0.3);
  border-radius: 4px;
}

.main-content::-webkit-scrollbar-thumb:hover {
  background: rgba(74, 144, 226, 0.5);
}

/* Tablet Responsive (768px - 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  :root {
    --header-height-desktop: 65px;
    --content-padding-desktop: 1.25rem;
  }

  .app-container {
    margin-left: 0; /* Hide sidebar on tablet */
    height: calc(100dvh - var(--header-height-desktop));
  }

  .main-content {
    padding: var(--content-padding-tablet);
  }
}

/* Mobile Responsive (up to 768px) */
@media (max-width: 768px) {
  :root {
    --header-height-desktop: var(--header-height-mobile);
    --content-padding-desktop: var(--content-padding-mobile);
  }

  .App {
    height: 100vh;
    height: 100dvh;
    overflow: hidden;
    /* Safe area for notched devices */
    padding-top: env(safe-area-inset-top);
    padding-bottom: env(safe-area-inset-bottom);
  }

  .app-container {
    margin-left: 0;
    flex-direction: column;
    height: calc(100vh - var(--header-height-mobile));
    height: calc(100dvh - var(--header-height-mobile));
    /* Account for safe area */
    height: calc(100dvh - var(--header-height-mobile) - env(safe-area-inset-top) - env(safe-area-inset-bottom));
  }

  .main-content {
    padding: var(--content-padding-mobile);
    padding-bottom: calc(var(--content-padding-mobile) + 70px); /* Space for bottom nav */
    margin-top: 0;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    /* Better mobile scrolling */
    overscroll-behavior: contain;
  }

  /* Improve touch targets */
  button, a, [role="button"] {
    min-height: var(--touch-target-min);
    min-width: var(--touch-target-min);
    /* Better tap highlight */
    -webkit-tap-highlight-color: rgba(74, 144, 226, 0.2);
  }

  /* Better spacing for mobile */
  .card {
    margin-bottom: 0.75rem;
    padding: 1rem;
  }

  /* Typography scaling */
  h1 { font-size: clamp(1.25rem, 4vw, 1.75rem); }
  h2 { font-size: clamp(1.1rem, 3.5vw, 1.5rem); }
  h3 { font-size: clamp(1rem, 3vw, 1.25rem); }
  p { font-size: clamp(0.875rem, 2.5vw, 1rem); }
}

/* Small Mobile (up to 480px) */
@media (max-width: 480px) {
  :root {
    --content-padding-desktop: var(--content-padding-small);
  }

  .app-container {
    height: calc(100dvh - var(--header-height-mobile));
  }

  .main-content {
    padding: var(--content-padding-small);
    padding-bottom: calc(var(--content-padding-small) + 70px); /* Space for bottom nav */
  }

  /* Prevent zoom on input focus (iOS) */
  input, select, textarea {
    font-size: 16px !important; /* Prevents zoom on iOS */
  }

  .card {
    padding: 0.75rem;
    margin-bottom: 0.5rem;
  }

  /* Smaller touch targets but still accessible */
  button, a {
    min-height: 40px;
    min-width: 40px;
  }
}

/* Extra Small Mobile (up to 375px) */
@media (max-width: 375px) {
  .App {
    height: 100vh;
    height: 100dvh;
    overflow: hidden;
  }

  .app-container {
    height: calc(100dvh - var(--header-height-mobile));
  }

  .main-content {
    padding: 0.4rem;
    padding-bottom: calc(0.4rem + 70px); /* Space for bottom nav */
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }

  h1 { font-size: 1.1rem; }
  h2 { font-size: 1rem; }
  h3 { font-size: 0.9rem; }

  .card {
    padding: 0.5rem;
    margin-bottom: 0.4rem;
  }
}

/* Landscape Mobile */
@media (max-width: 768px) and (orientation: landscape) {
  .app-container {
    height: calc(100dvh - var(--header-height-mobile));
  }

  .main-content {
    padding: 0.5rem;
    padding-bottom: calc(0.5rem + 70px); /* Space for bottom nav */
  }
}

/* Touch-friendly improvements */
@media (hover: none) and (pointer: coarse) {
  /* Remove hover effects on touch devices */
  button:hover,
  .card:hover,
  a:hover {
    transform: none;
  }

  /* Better active states for touch */
  button:active,
  a:active {
    opacity: 0.7;
    transform: scale(0.98);
  }
}

/* High DPI displays */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .main-content {
    /* Better rendering on retina displays */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

/* Print styles */
@media print {
  .app-container {
    margin-left: 0;
  }

  .main-content {
    padding: 0;
    overflow: visible;
  }
}

/* Utility Classes */
.card {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-primary {
  background: linear-gradient(135deg, rgba(74, 144, 226, 0.9) 0%, rgba(80, 201, 206, 0.8) 100%);
  color: #FFFFFF;
  font-weight: 600;
}

.btn-primary:hover {
  background: linear-gradient(135deg, rgba(74, 144, 226, 1) 0%, rgba(80, 201, 206, 0.95) 100%);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(74, 144, 226, 0.4);
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
}

.btn-secondary:hover {
  background-color: #5a6268;
}

.btn-success {
  background-color: #28a745;
  color: white;
}

.btn-success:hover {
  background-color: #218838;
}

.btn-danger {
  background-color: #dc3545;
  color: white;
}

.btn-danger:hover {
  background-color: #c82333;
}

.status-badge {
  display: inline-block;
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-size: 0.875rem;
  font-weight: 500;
}

.status-pending {
  background-color: #fff3cd;
  color: #856404;
}

.status-in-preparation {
  background-color: #cce5ff;
  color: #004085;
}

.status-ready {
  background-color: #d4edda;
  color: #155724;
}

.status-delivered {
  background-color: #d1ecf1;
  color: #0c5460;
}

/* Loading and Error States */
.loading-container,
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  text-align: center;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(74, 144, 226, 0.1);
  border-top: 4px solid rgb(var(--color-primary));
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-message {
  color: #dc3545;
  font-size: 1.1rem;
  margin-bottom: 1rem;
}

.error-container button {
  padding: 0.75rem 1.5rem;
  background: rgb(var(--color-primary));
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
}

.error-container button:hover {
  background: rgb(var(--color-primary-dark));
}

/* Responsive Utilities */
.container-responsive {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 var(--content-padding-desktop);
}

@media (max-width: 768px) {
  .container-responsive {
    padding: 0 var(--content-padding-mobile);
  }
}

/* Grid Responsive */
.grid-responsive {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}

@media (max-width: 768px) {
  .grid-responsive {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
}

/* Flex Responsive */
.flex-responsive {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

@media (max-width: 768px) {
  .flex-responsive {
    flex-direction: column;
    gap: 0.75rem;
  }
}
