@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

@layer base {
  :root {
    /* Soft Blue-Teal Professional Theme - Eye-Friendly for Long Viewing */
    --color-primary: 74 144 226; /* Soft Blue #4A90E2 */
    --color-primary-dark: 59 130 246; /* Darker Blue #3B82F6 */
    --color-secondary: 80 201 206; /* Teal #50C9CE */
    --color-accent: 107 207 127; /* Soft Green #6BCF7F */
    --color-success: 16 185 129; /* Emerald #10B981 */
    --color-warning: 245 158 11; /* Amber #F59E0B */
    --color-error: 239 68 68; /* Red #EF4444 */
    --color-ai: 80 201 206; /* Teal for AI elements */
    --color-background: 248 250 252; /* Very Light Gray-Blue #F8FAFC */
    --color-surface: 255 255 255; /* Pure White */
    --color-text: 30 41 59; /* Dark Slate #1E293B */
    --color-text-secondary: 100 116 139; /* Medium Slate #64748B */
    --gradient-primary: linear-gradient(135deg, rgba(74, 144, 226, 0.9) 0%, rgba(80, 201, 206, 0.8) 100%);
    --gradient-blue: linear-gradient(135deg, #4A90E2 0%, #3B82F6 100%);
    --gradient-teal: linear-gradient(135deg, #50C9CE 0%, #14B8A6 100%);
    --gradient-premium: linear-gradient(135deg, rgba(74, 144, 226, 0.95) 0%, rgba(74, 144, 226, 0.7) 50%, rgba(80, 201, 206, 0.8) 100%);
    --gradient-success: linear-gradient(135deg, #10b981 0%, #059669 100%);
    --gradient-warning: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
    --gradient-error: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  }

  .dark {
    --color-primary: 96 165 250; /* Bright Blue #60A5FA */
    --color-primary-dark: 59 130 246; /* Darker Blue #3B82F6 */
    --color-secondary: 52 211 153; /* Soft Teal #34D399 */
    --color-accent: 129 140 248; /* Soft Indigo #818CF8 */
    --color-success: 52 211 153; /* Emerald #34D399 */
    --color-warning: 251 191 36; /* Amber #FBBF24 */
    --color-error: 248 113 113; /* Red #F87171 */
    --color-ai: 52 211 153; /* Teal for AI elements */
    --color-background: 15 23 42; /* Deep Slate #0F172A */
    --color-surface: 30 41 59; /* Dark Slate #1E293B */
    --color-text: 241 245 249; /* Light Slate #F1F5F9 */
    --color-text-secondary: 148 163 184; /* Medium Light Slate #94A3B8 */
    --gradient-primary: linear-gradient(135deg, rgba(96, 165, 250, 0.9) 0%, rgba(52, 211, 153, 0.8) 100%);
    --gradient-blue: linear-gradient(135deg, #60A5FA 0%, #3B82F6 100%);
    --gradient-teal: linear-gradient(135deg, #34D399 0%, #14B8A6 100%);
    --gradient-premium: linear-gradient(135deg, rgba(96, 165, 250, 0.95) 0%, rgba(96, 165, 250, 0.7) 50%, rgba(52, 211, 153, 0.8) 100%);
    --gradient-success: linear-gradient(135deg, #34D399 0%, #10B981 100%);
    --gradient-warning: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
    --gradient-error: linear-gradient(135deg, #f87171 0%, #ef4444 100%);
  }

  body {
    background-color: rgb(var(--color-background));
    color: rgb(var(--color-text));
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transition: background-color 0.3s ease, color 0.3s ease;
    /* Prevent horizontal scroll */
    overflow-x: hidden;
    max-width: 100vw;
    /* Better touch handling */
    touch-action: pan-y;
    /* Prevent text selection on mobile (optional) */
    -webkit-tap-highlight-color: rgba(74, 144, 226, 0.2);
  }

  /* Better focus styles for accessibility */
  *:focus-visible {
    outline: 2px solid rgb(var(--color-primary));
    outline-offset: 2px;
    border-radius: 4px;
  }

  /* Prevent text size adjustment on iOS */
  @media (max-width: 768px) {
    html {
      -webkit-text-size-adjust: 100%;
      text-size-adjust: 100%;
    }
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  }
}

@layer components {
  .card-modern {
    @apply bg-white dark:bg-slate-800 rounded-xl shadow-soft p-6 border border-gray-100 dark:border-slate-700 transition-all duration-300 hover:shadow-lg;
  }

  .btn-modern {
    @apply px-4 py-2 rounded-lg font-medium transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed;
  }

  .btn-primary-modern {
    @apply btn-modern bg-primary-600 text-white hover:bg-primary-700 focus:ring-primary-500 active:scale-95;
  }

  .input-modern {
    @apply w-full px-4 py-2 border border-gray-300 dark:border-slate-600 rounded-lg bg-white dark:bg-slate-700 text-gray-900 dark:text-slate-100 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all;
  }
}